import React from "react";
import Link from "next/link";
import LibInfoBanner from "../components/LibInfoBanner";
import DynamicTableOfContents from "../components/DynamicTableOfContents";

export const Sidebar = ({ children }) => (
    <div id="sidebar">
        <style jsx global>{`
            #sidebar {
                background: #fff;
                position: fixed;
                top: 0;
                left: 0;
                bottom: 0;
                width: 250px;
                overflow-y: auto;
                overflow-x: auto;
                padding: 10px 30px 30px 30px;
                border-right: 1px solid #bbb;
                box-shadow: 0 0 20px #ccc;
            }
            #sidebar a,
            #sidebar a:visited {
                display: block;
                color: #000;
                font-weight: 700;
                margin-top: 5px;
                text-decoration: none;
            }
            .toc-children {
                margin: 10px 0 0;
                border-left: 3px solid #ddd;
                padding-left: 10px;
            }
            #sidebar h1,
            #sidebar h2,
            #sidebar h3,
            #sidebar h4,
            #sidebar h5,
            #sidebar h6 {
                font-size: 0.75rem !important;
                font-weight: 600;
            }
            .sidebar-section-header {
                background: #ddd;
                padding: 5px;
                text-transform: uppercase;
                border-radius: 4px;
                color: gray;
            }
        `}</style>
        <LibInfoBanner />
        <h1 className="sidebar-section-header">Tour of GRelDAL</h1>
        <Link href={`${ROOT_PATH}/`}>
            <a>⚡ Quick Start</a>
        </Link>
        <Link href={`${ROOT_PATH}/mapping-operation`}>
            <a>⚡ Mapping Operations</a>
        </Link>
        <Link href={`${ROOT_PATH}/mapping-associations`}>
            <a>⚡ Mapping Associations</a>
        </Link>
        <Link href={`${ROOT_PATH}/best-practices`}>
            <a>⚡ Best Practices</a>
        </Link>
        <h1 className="sidebar-section-header">Additional Topics</h1>
        <Link href={`${ROOT_PATH}/runtime-types`}>
            <a>⚡ Runtime Types</a>
        </Link>
        <Link href={`${ROOT_PATH}/comparision-with-alternatives`}>
            <a>⚡ Comparision With Alternatives</a>
        </Link>
        <hr />
        <Link href={`${ROOT_PATH}/api`}>
            <a>API</a>
        </Link>
        <h1 className="sidebar-section-header">Page Outline</h1>
        <DynamicTableOfContents />
        {children}
    </div>
);
